*{overflow-wrap:break-word;word-wrap:break-word}#preview-box{margin:20px;box-shadow:0 0 15px 1px gray;padding:20px;z-index:1;border-radius:10px}#preview-box #preview{height:100%;width:100%;font-size:14px;font-family:'Open Sans';padding:10px}#preview-box #preview h1{border-bottom:3px solid #f2f2f2}#preview-box #preview h2{font-size:20px;font-weight:bold}#preview-box #preview pre,#preview-box #preview code{background-color:#f2f2f2;color:black;font-size:14px}#preview-box #preview pre{margin:10px;max-width:95%;padding:5px}#preview-box #preview blockquote{font-size:16px;text-align:left;border-left:3px solid green;padding:0 8px;color:#4d4d4d;margin-left:30px}#preview-box #preview table{border:1px solid gray;max-width:95%;margin:10px;word-break:break-all}#preview-box #preview table thead{background-color:#f2f2f2}#preview-box #preview table thead th,#preview-box #preview table thead tr{padding:5px 10px;border-bottom:1px solid gray;border-right:1px dashed gray;text-align:center}#preview-box #preview table tbody{font-size:12px}#preview-box #preview table tbody td,#preview-box #preview table tbody th{padding:5px 10px;border-bottom:1px dotted gray;border-right:1px dashed gray}#preview-box #preview a{color:green}#preview-box #preview a:hover{opacity:0.5;text-decoration:none}#preview-box #preview p{margin-bottom:5px}

.editor-maximized #editor{width:100%;height:200px;min-height:20px;box-sizing:border-box;padding:0 5px;position:relative;color:green;background-color:black;z-index:100;resize:vertical;outline:none;border:1px solid black;border:none;overflow-y:scroll;font-family:'Inconsolata', monospace;font-size:13px}@media (min-width: 700px){.editor-maximized #editor-box{position:fixed;width:300px;margin:10px auto;right:5px}.editor-maximized #editor-box #editor{opacity:0.8}.editor-maximized #editor-box #editor:hover{opacity:1}}@media (min-width: 900px){.editor-maximized #editor-box{width:500px}}@media (max-width: 700px){.editor-maximized #editor-box{width:70vw;margin:10px auto}.editor-maximized #editor-box #editor{opacity:1}}.editor-minimized #editor{display:none}

